<template>
    <div>
        <div class="page">
            <open v-if="active===0"></open>
            <all v-if="active===1"></all>
            <timetable v-if="active===2"></timetable>
            <my v-if="active===3"></my>
            <div>
                <van-tabbar :fixed="fixed" v-model="active" @change="changeFn" active-color="#00B095"
                            inactive-color="#7F8188"
                            :safe-area-inset-bottom="fixed">
                    <van-tabbar-item v-for="(i,index) in tab" :key="index">
                        <span>{{ i.name }}</span>
                        <template #icon="props">
                            <img :src="props.active ? i.active : i.inactive"/>
                        </template>
                    </van-tabbar-item>
                </van-tabbar>
            </div>
        </div>
    </div>
</template>

<script>
    import open from './comm/open'
    import timetable from './comm/timetable'
    import my from './comm/my'
    import all from './comm/all'

    export default {
        name: "home",
        components: {
            open,
            timetable,
            my,
            all
        },
        data() {
            return {
                fixed: true,
                view: false,
                amtran: true,
                active: 0,
                tab: [{
                    name: '正在开课',
                    active: require('./img/live_home_ing_selected@2x.png'),
                    inactive: require('./img/live_home_ing_normal@2x.png'),
                }, {
                    name: '全部课程',
                    active: require('./img/live_home_class_selected@2x.png'),
                    inactive: require('./img/live_home_class_normal@2x.png'),
                }, {
                    name: '课程表',
                    active: require('./img/live_home_schedule_selected@2x.png'),
                    inactive: require('./img/live_home_schedule_normal@2x.png'),
                }, {
                    name: '我的',
                    active: require('./img/live_home_my_selected@2x.png'),
                    inactive: require('./img/live_home_my_normal@2x.png'),

                }]
            }
        },
        created() {
            if (sessionStorage.getItem('type')) {
                this.active = Number(sessionStorage.getItem('type'))
            }
        },
        methods: {
            changeFn(e) {
                this.active = e;
                window.scrollTo(0, 0);
                sessionStorage.setItem('type', e)
            }
        }
    }
</script>

<style scoped>
    .page {
        padding-bottom: 200px;
        height: 100vh;
        box-sizing: border-box;
    }
</style>
