                            <h3>{{ i.courseName }}</h3>
                            <p>{{i.startTime }}</p>
                        </div>
                        <div class="item-top-left-right">
                            <img :src="i.memberIcon ? i.memberIcon : 'https://uniondrug-release.oss-cn-shanghai.aliyuncs.com/backend.app/2h80j6hdfu3o35tb3evlj9f3as.png'" alt="">
                            <div>
                                <h3>{{ i.teacherName }}</h3>
                                <p>{{ i.teacherTitle }}</p>
                            </div>
                        </div>
                    </div>
                    <div class="item-top-right">
                        <img :src="i.coursePictureUrl + '?x-oss-process=image/resize,w_600/quality,q_80'" alt="">
