                    </div>
                </div>
                <div v-if="i.dataStatus == 1">
                    <div class="item-footer" v-if="i.liveStatus=='0' && i.myPublish == '1'">
                        <div></div>
                        <button class="btn bt2">等待开课</button>
                    </div>

                    <div class="item-footer" v-if="i.liveStatus=='0' && i.myPublish == '0'">
                        <div></div>
                        <button class="opctic" v-if="i.mySubscribe == '0'">预约听课</button>
                        <button class="opctic" v-if="i.mySubscribe == '1'">取消预约</button>
                    </div>

                    <div class="item-footer" v-if="i.liveStatus=='1' && i.myPublish == '1' && i.courseType != '4'">
