                        <div></div>
                        <button class="opctic" @click="detailNav(i.courseId)">审核中</button>
                    </div>
                </div>

            </div>
        </div>
        <div v-if="view">
            <div class="none" v-if="list.length===0">
                <img src="../../img/live_default_page@2x.png" alt="">
                <p>{{ tost }}</p>
            </div>
        </div>
    </div>
