<script>
    export default {
        name: "tk",
        props: ['channel', 'tost'],
        data() {
            return {
                list: [],
                view: false,
                "subscribeCount": "0",
                "finishCount": "0",
                "unFinishCount": "0",
                value: null,
            }
        },
        watch: {
            channel(val, oldVal) {//普通的watch监听
                console.log("a: " + val, oldVal);
            },
        },
        created() {
            this.listing()
            this.statistic()
        },
        methods: {
            onCancel() {

            },
            onSearch(val) {
                this.value = val;
                this.listing()
            },
            checkFive(startTime){
                var str = new Date(startTime);
                var now = Date.now();
                if(str > (now+5*60*1000)){
                    return false
                }else{
                    return true
                }
            },
            listing() {
                this.$bs.getVersion({}).then((e) => {
                    this.$api.listing({
                        version: e.data.data,
                        "channel": this.channel,
                        name: this.value
                    }).then(res => {
                        this.list = res.body;
                        this.view = true;
                        this.list.forEach((i)=>{
                            if(i.liveStatus == '0'){
                                if(i.isAfterFiveMin == '1'){
                                    i.liveStatus = '1'
                                }
                            }
                        })
                    })
                })

            },

            statistic(){
                this.$api.statistic({
                    "type": this.channel == '3' ? '1': '2'
                }).then(res => {
                    this.subscribeCount = res.subscribeCount
                    this.finishCount = res.finishCount
                    this.unFinishCount = res.unFinishCount
                })
            },
            detailNav(courseId) {
                this.$router.push('/yaodianbao/openClass/detail?courseId=' + courseId + '&channel=' + this.channel)
            },
            courseTypeFn(type){
                if(type === '1'){
                    return '学术会'
                }
                if(type === '2'){
                    return '患教会'
                }

                if(type === '3'){
                    return '连锁内训'
                }
            },

        }
    }
</script>

