<template>
   <div>
       <div class="tab">
           <van-tabs  @change="changeFn" v-model="activeName" line-width="30" line-height="4">
               <van-tab title="听课" name="0">
                   <div class="tab-top">
                       <tk channel="3" tost="您还未预约任何课程，快去寻找心仪的课程吧~"></tk>
                   </div>
               </van-tab>
               <van-tab title="讲课" name="1">
                   <div class="tab-top">
                       <tk channel="4" tost="暂无课程"></tk>
                   </div>
               </van-tab>
           </van-tabs>
       </div>
   </div>
</template>

<script>
    import tk from './tk/tk'

    export default {
        name: "timetable",
        components: {
            tk
        },
        data() {
            return {
                activeName: '0',
            }
        },
        created(){
            if(sessionStorage.getItem('activeName')){
                this.activeName = sessionStorage.getItem('activeName')
            }
        },
        methods:{
            changeFn(e){
                this.activeName = e;
                window.scrollTo(0, 0);
                sessionStorage.setItem('activeName', e)
            }
        }
    }
</script>

<style scoped>
    .tab {
        width: 40%;
    }

    /deep/ .van-tab span {
        font-size: .32rem;
        font-weight: bold;
        color: #B0B3BC;
    }

    /deep/ .van-tab--active span {
        font-size: .34rem !important;
        color: #333333 !important;
    }

    /deep/ .van-tabs__line {
        background: #00B095;
        border-radius: 4px;
    }
</style>
